import React from 'react';
import ReactDOM from 'react-dom';
import './App.css';
import Web3 from 'web3';
import { ADDRESS, ABI } from './config';
import RegisterForm from './components/RegisterForm';
import BiddingForm from './components/BiddingForm';
import RevealWinnersButton from './components/RevealWinnersButton';

class App extends React.Component {
  constructor() {
    super()
    this.state = {
     bidderCount:0, 
     beneficiary : 0
    }
  }

  componentDidMount() {
    this.load()
  }

  async load() {
    const web3 = new Web3(Web3.givenProvider);
    const Auction = new web3.eth.Contract(ABI, ADDRESS);
     Auction.methods.beneficiary().call().then((res) => {this.setState({ bidderCount : 0,
      beneficiary : res})}) 
   
  }

  render() {
    return (
      <div >
        <div>
        Bidder Count : {this.state.bidderCount}
        <br/>
        Beneficiary : {this.state.beneficiary}
        </div>
       <RegisterForm/>
       <BiddingForm/>
       <RevealWinnersButton/>
      </div>
    );
  }
}

export default App;
