import React from "react"

import Web3 from 'web3';
import { ABI, ADDRESS } from '../config'
import ItemWinner from "./ItemWinner";
class RevealWinnersButton extends React.Component {
    constructor(){
        super()
       this.handleSubmit = this.handleSubmit.bind(this) 
       this.state = {
           firstItemWinner : "",
           secondItemWinner : "",
           thirdItemWinner : ""
       }
    }

    handleSubmit(event){
        event.preventDefault()
        const web3 = new Web3(Web3.givenProvider)
        web3.eth.getAccounts().then((accounts) =>{
            const auction = new web3.eth.Contract(ABI, ADDRESS)
            auction.methods.revealWinners().call({from : "0x78d86E031605d2873f38dfa2657Fe92d8aC243f9"}).then(() => {
                 auction.methods.winners(0).call().then((response) => {
                    this.setState ({
                        firstItemWinner : response
                    })
                })
                auction.methods.winners(1).call().then((response) => {
                   this.setState ({
                       secondItemWinner : response
                   })
               })
               auction.methods.winners(2).call().then((response) => {
               this.setState ({
                   thirdItemWinner : response
               })
           })
            })
        })
    }

    render() {
        
        return(
            <div>
            <form onSubmit = {this.handleSubmit}>
            <button type = "submit">Reveal Winners</button>
            </form>
            <ItemWinner item = {1} winner = {this.state.firstItemWinner} />
            <ItemWinner item = {2} winner = {this.state.secondItemWinner} />
            <ItemWinner item = {3} winner = {this.state.thirdItemWinner} />
            </div>
        )
    }
}
export default RevealWinnersButton