    import React from "react";
    
    import Web3 from 'web3';
    import { ABI, ADDRESS } from '../config';

class BiddingForm extends React.Component {
 constructor (){
     super()
     this.state = {
         id : 0,
         numOfTokens:0
     }
     this.handleSubmit = this.handleSubmit.bind(this)
     this.handleChangeId = this.handleChangeId.bind(this)
     this.handleChangeToken = this.handleChangeToken.bind(this)
 }

 handleSubmit (event){
     event.preventDefault()
     const web3 = new Web3(Web3.givenProvider)
     web3.eth.getAccounts().then((accounts) =>{
        const auction = new web3.eth.Contract(ABI, ADDRESS)
        auction.methods.bid(this.state.id, this.state.numOfTokens).send({from : accounts[0]}).then(() => {})
    })
 }

 handleChangeId(event){
     this.setState({
        id : event.target.value
     })
 }

 handleChangeToken(event){
    this.setState({
       numOfTokens : event.target.value
    })
}

render (){
return (<div>
    <form onSubmit = {this.handleSubmit}>
        <input type = "text" value = {this.state.id} placeholder = "item id" onChange = {this.handleChangeId}/>
        <input type = "text" value = {this.state.numOfTokens} placeholder = "number of tokens" onChange = {this.handleChangeToken}/>
        <br/>
        <button type = "submit">Bid</button>
    </form>
</div>)
}
}
export default BiddingForm